import { Component, OnInit, AfterViewInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { AdsService } from '../ads.service';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-edit-ad',
  templateUrl: './edit-ad.component.html',
  styleUrls: ['./edit-ad.component.scss'],
})
export class EditAdComponent implements OnInit, AfterViewInit {
  image;
  adId;
  constructor(
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private adsService: AdsService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private router: Router
  ) {}

  form = this.fb.group({
    image: [],
  });

  ngOnInit(): void {
    this.adId = this.route.snapshot.paramMap.get('id');
  }
  ngAfterViewInit() {
    this.getOne(this.adId);
  }

  getOne(id) {
    this.spinner.show();

    this.adsService.getOne(id).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.image = res['data']['image'];
        this.form.get('image').patchValue(res['data']['image']);
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  onSubmit(formValue) {
    this.spinner.show();

    const formData = new FormData();

    for (const key in formValue) {
      if (formValue[key] instanceof File) {
        formData.append(key, formValue[key]);
      } else {
        // contains object
        formData.append(key, JSON.stringify(formValue[key]));
      }
    }

    this.adsService.updateOne(formData, this.adId).subscribe(
      (res) => {
        console.log(res);
        this.image = null;
        this.form.reset();
        this.toastr.info(res['message'], 'Info');
        this.router.navigate(['/ads']);
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }
}
